import React from "react";
import styled from "styled-components";

function Title({ title, span }) {
  return (
    <TitleStyled>
      <h2>
        {title}{" "}
        <b>
          <span>{span}</span>
        </b>
      </h2>
    </TitleStyled>
  );
}

const TitleStyled = styled.div`
    position: relative;
    h2{
        color: var(--white-color);
        font-size: 3.1rem;
        font-weight: 600;
        text-transform: uppercase;
        position: relative;
        padding-bottom: .7rem;
        @media screen and (max-width: 496px){
            font-size: 2.8rem;
        }
        @media screen and (max-width: 370px){
            font-size: 2rem;
        }
        &::before{
            content: "";
            position: absolute;
            bottom: 0;
            width: 7.4rem;
            height: .33rem;
            background-color: var(--background-light-color-2);
            border-radius: 15px;
            left: 0;
        }
        &::after{
            content: "";
            position: absolute;
            bottom: 0;
            width: 3.5rem;
            height: 0.33rem;
            background-color: var(--primary-color);
            border-radius: 15px;
            left: 0;
            animation: ani 2.7s infinite;
        }
        
        
        @keyframes ani {
          0% {
            left: 0;
          }
          50% {
            left: 70px;
          }
          100% {
            left: 0;
          }
        }
        }
        span{
            font-weight: 900;
            color: var(--background-light-color-2);
            font-size: 5rem;
            position: absolute;
            left: 0;
            top: 30%;
            z-index: -1;
            transform: translateX(1000px);
            animation-delay: -3s;
            
            animation: cross 5s forwards;

            @keyframes cross{
                0%{
                    transform: translateX(1000px);
                }
                100%{
                    transform: translateX(0px);
                }
                
            }
            @media screen and (max-width: 620px){
                font-size: 4rem;
            }
            @media screen and (max-width: 496px){
                font-size: 3rem;
            }
            @media screen and (max-width: 370px){
                font-size: 2rem;
            }
        }
        
    }
`;

export default Title;
